.overview-container {
  padding: 10px 30px;
  margin: 20px;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  border-radius: 15px;
  box-shadow: $box-shadow-black-course;

  .overview-header {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  }

  .overview-mark {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 100;
    margin: auto 0;
    font-size: 40px;

    .overview-gpa {
      font-size: 20px;
      font-style: italic;
      font-weight: 500;
    }

    .awesome {
      color: $awesome;
    }

    .good {
      color: $good;
    }

    .okay {
      color: $okay;
    }

    .bad {
      color: $bad;
    }
  }
}